@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap");
@import 'components/variables';
@import 'components/mixins';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: $font-roboto;
  color: $dark-grey;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: $light-grey;
}

.todo-app_header h1 {
  font-size: 100px;
  font-weight: 100;
  letter-spacing: -2.5px;
  color: $grey;
}

.todo-app_main {
  position: relative;
  max-width: 562px;
  width: 100%;
  background-color: #ffffff;
  display: block;
  border: 1px solid white;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.todo-app_search_bar {
  display: flex;
  height: 66px;
  border-bottom: 1px solid #e5e5e5;
}

.todo-app_search_bar__hide_button {
  background-image: url("../images/select-all.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: #ffffff;
  margin: 21px 15px;
  padding: 10px 15px;
  border: 0;
  font-size: 0;
  cursor: pointer;
}

.todo-app_search_bar__input_field {
	input {
    height: 100%;
    width: 100%;
    margin-right: 0;

    font-style: italic;  
    line-height: 21px;
    @include font-setting(24px, 300, -0.6px);
	
    border: 0;
  }

  input:focus {
    outline: none;
  }
}

.todo-app_tasks {
  max-height: 296px;
  overflow-y: auto;
}

.todo-app_task-item {
  display: flex;
  padding: 12px 10px;
  cursor: pointer;
  border-bottom: 1px solid $light-grey-2;
}

.todo-app_task-item:hover {
  background-color: $off-white;
}

.task-item_checkbox {
  appearance: none;
  align-self: center;
  width: 40px;
  height: 39px;
  border: 1px solid $light-grey-2;
  border-radius: 50%;
  background-color: #ffffff;
}

.task-item_checkbox:focus,
.task-item_checkbox:hover {
  border: 1px solid $green;
}

.task-item_checkbox:checked {
  background-image: url("../images/check.svg");
  background-repeat: no-repeat;
  background-position: center;
  border: 1px solid $green;
}

.task-item_text {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-style: normal;
  @include font-setting(24px, 300, -0.6px);
  color: #666666;
  margin-left: 10px;
  margin-right: auto;
  align-self: center;
}

.task-item_delete {
  background-image: url("../images/delete.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: #ffffff;
  opacity: 0;
  padding: 15px;
  border: 0;
  font-size: 0;
  cursor: pointer;
}

.task-item_delete:focus {
  opacity: inherit;
}

.todo-app_task-item:hover > .task-item_delete {
  opacity: inherit;
  background-color: $off-white;
}

.todo-app_footer {
  height: 43px;
  box-shadow: inset 0px 1px 0px $light-grey-2;
  margin: 0 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.todo-app_footer__items_left {
  line-height: 21px;
  @include font-setting(14px, 300, -0.35px);
  color: $dark-grey;
}

.todo-app_footer__filter {
  cursor: pointer;
  margin-left: 50px;
}

.radio-filter {
  width: 65px;
  margin-right: -80px;
  opacity: 0;
  cursor: pointer;
}

.radio-filter_label {
  border-radius: 10%;
  padding: 3px 10px;
  display: inline-block;
  border: 1px solid #0000;
}

.radio-filter:checked + .radio-filter_label {
  border: 1px solid #ebdfdf;
}

.radio-filter:focus + .radio-filter_label,
.radio-filter:hover + .radio-filter_label {
  border: 1px solid $light-grey-2;
}

.todo-app_footer__clear_button button {
  padding: 5px;
  margin-left: 30px;
  background-color: #ffffff;
  border: 0;
}

.todo-app_footer__clear_button button:hover {
  cursor: pointer;
  color: #000000;
}

.todo-app_box-one {
  background-color: #ffffff;
  max-width: 552px;
  width: 98%;
  height: 5px;
  z-index: -1;
  box-shadow: 0px 2px 4px 0px #00000033;
}

.todo-app_box-two {
  background-color: #ffffff;
  max-width: 542px;
  width: 96%;
  height: 4px;
  z-index: -2;
  box-shadow: 0px 2px 4px 0px #00000033;
}
